// Virtual clicker component
import VirtualClickerContainer from './components/virtual clicker/clicker-container';
import VirtualClickerMinimized from './components/virtual clicker/clicker-minimized';

import VerticalMenuDefault from './components/vertical menu/default/vertical-menu-default';
import MenuPopup from './components/menu popup/menu-popup'; 

// Screens
import InstructionsScreen from './components/instructions screen/instructions-screen';
import RegisterScreen from './components/register screen/register-screen';
import AttendanceScreen from './components/attendance screen/attendance-screen';
import AttendanceScreenPopup from './components/attendance screen/attendance-screen-popup';

import WebScreen from './components/web screen/web-screen';

const appVersion = "7.0";

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      VirtualClicker: false,
      VirtualClickerParams: {
          host: "",
          participant: ""
      },
      VirtualClickerMaximized: true,
      VirtualClickerMinimizedPositionLeft: null,
      VerticalMenuDefault: false,
      VerticalMenuContent: "instructionScreen",
      currentScreen: "introPage",
      InstructionsScreen: {
        visible: false,
        componentClass: "",
        type: ''
      },
      InitialScreen: {
        // visible: true,
        componentClass: ""
      },
      RegisterScreen: {
        visible: false,
        componentClass: ""
      },
      AttendanceScreen: {
        visible: false,
        componentClass: "",
        popupActive: false
      },
      WebSessionScreen: {
        visible: false,
        componentClass: "",
        sessionID: "HOP",
        sessionPassword: "default-pass-123",
        sessionType: ''
      },
      MenuPopupActive: false,
      
      // MenuPopupSettings: {

      // }

      RegisteredStudents: {},


    };

    // This binding is necessary to make `this` work in the callback
    this.minimizeClicker = this.minimizeClicker.bind(this);
    this.maximizeClicker = this.maximizeClicker.bind(this);

    this.call2Clicker = this.call2Clicker.bind(this);
    this.initialScreenCall2Parent = this.initialScreenCall2Parent.bind(this);
    this.verticalMenuCall2Parent = this.verticalMenuCall2Parent.bind(this);
    this.attendanceScreenPopup = this.attendanceScreenPopup.bind(this);

    this.menuPopupCallback = this.menuPopupCallback.bind(this);

    this.WebSessionScreenAction = this.WebSessionScreenAction.bind(this);
    
    
  };

  componentWillMount() {
      document.title = "LivePoll App v." + appVersion;
  }

  WebSessionScreenAction (arg) {
    // console.log(arg.target)
    console.log("WebSession " + arg.target.id)

    let newWebSessionScreen = this.state.WebSessionScreen;

    if (arg.target.id === "webscreen-back-button") {
      newWebSessionScreen.componentClass = 'webScreen-move-2-right';
      newWebSessionScreen.visible = false;
  
      this.setState({
          'currentScreen': "WebSessionScreenPage",
          'WebSessionScreen': newWebSessionScreen,
          'InitialScreen': {
            visible: true,
            componentClass: 'initialScreen-move-2-right'
          }
      })
    }

    if (arg.target.id === "webscreen-start-button") {
      newWebSessionScreen.componentClass = 'webScreen-move-2-left-out';
      newWebSessionScreen.visible = false;
  
      // this.setState({
      //     'currentScreen': "WebSessionScreenPage",
      //     'WebSessionScreen': newWebSessionScreen,
      //     'InitialScreen': {
      //       visible: true,
      //       componentClass: 'initialScreen-move-2-right'
      //     }
      // })

      // if (this.state.InitialScreen.type)
      
      this.setState ({
        'currentScreen': "instructionsPage",
        'WebSessionScreen': newWebSessionScreen,
        'InstructionsScreen': {
          visible: true,
          componentClass: 'instructionsScreen-move-2-left',
          // type: this.state.InstructionsScreen.type
          // type: 'clicker'
          type: this.state.InstructionsScreen.type
        },
        // 'InitialScreen': {
          // visible: false,
          // componentClass: 'initialScreen-move-2-left'
        // },
        'VerticalMenuDefault' : true,
      })

    }

    
  }

  menuPopupCallback() {
    this.setState({'MenuPopupActive': false});
    console.log("menuPopupCallback calling");
    // this.verticalMenuCall2Parent("closeMenuPopup");
  }

  minimizeClicker (slide2left) {
    this.setState({VirtualClickerMaximized: false});
    this.setState({VirtualClickerMinimizedPositionLeft: slide2left});
    // if (this.state.clickerType === "large") {
      // TODO     ,    .  ,      .  , ,   -   , .
    document.getElementById('clicker-wrapper-large') ? document.getElementById('clicker-wrapper-large').style.display = "none" : document.getElementById('clicker-wrapper').style.display = "none";
  }

  maximizeClicker () {
    this.setState({VirtualClickerMaximized: true});
    // if (this.state.clickerType === "large") {

      document.getElementById('clicker-wrapper-large') ? document.getElementById('clicker-wrapper-large').style.display = "block" : document.getElementById('clicker-wrapper').style.display = "block";
      // document.getElementById('clicker-wrapper-large').style.display = "block";
    // } else {
      // document.getElementById('clicker-wrapper').style.display = "block";
    // }
  }

  call2Clicker (host, participant) {
    // console.log(host + " & " + participant);
    // !this.state.VirtualClicker ? this.setState({'VirtualClicker': true}) : this.setState({'VirtualClicker': false});

    // if (host || participant || (host && participant)) {
    if (host === "active" || participant === "active" || (host === "active" && participant === "active")) {
        // this.setState({'VirtualClickerParams': {host: true}});
        let newState = this.state.VirtualClickerParams;
        newState.host = host;
        newState.participant = participant;
        this.setState({VirtualClickerParams: newState});

        this.setState({'VirtualClicker': true});
        // this.setState({'VirtualClickerParams': {participant: true}});
        // VirtualClickerContainer.host = arg1.bttn4;
    }
    else {
        this.setState({'VirtualClicker': false})
        this.setState({'VirtualClickerMaximized': true})
        // this.state.VirtualClickerMaximized
    }

  }

  // nextScreen (arg) {
    // console.log("next: " + arg)
    // this.setState({'currentScreen': 33})
  // }

  initialScreenCall2Parent (callerBttnName, sessionType, password) {
    // console.log(arg + " ho")

    // console.log(sessionType + " zzz")

    // console.log(arg1 + " and " + arg2)
    if (callerBttnName === "Start Session") {
      this.setState({
        'currentScreen': "instructionsPage",
        // 'InstructionsScreenClass': "instructionsScreen-move-2-left",
        // 'RegisterScreenClass': "registerScreen-move-2-left",
        // 'IntroScreenClass': "initialScreen-move-2-left",
        // 'AttendanceScreenClass': "attendanceScreen-move-2-up",
        'InstructionsScreen': {
          visible: true,
          componentClass: 'instructionsScreen-move-2-left',
          // type: this.state.InstructionsScreen.type
          // type: 'clicker'
          type: sessionType
        },
        'InitialScreen': {
          // visible: false,
          componentClass: 'initialScreen-move-2-left'
        },
        'VerticalMenuDefault' : true,
      })
    }

    if (callerBttnName === "Next") {

      let newWebSessionScreen = this.state.WebSessionScreen;
      newWebSessionScreen.visible = true;
      newWebSessionScreen.sessionType = sessionType;
      newWebSessionScreen.componentClass = 'webScreen-move-2-left';

      if (password) {
        newWebSessionScreen.sessionPassword = password;
      }

      //  :(
      let tempoUpdateInstructionScreen = this.state.InstructionsScreen;
      tempoUpdateInstructionScreen.type = sessionType;

      this.setState({
        'currentScreen': "WebSessionScreenPage",
        'WebSessionScreen': newWebSessionScreen,
        'InitialScreen': {
          // visible: false,
          componentClass: 'initialScreen-move-2-left',
          // type: sessionType
        },
        // //  :(
        'InstructionsScreen': tempoUpdateInstructionScreen
        // 'InstructionsScreen': {
        //   visible: true,
        //   componentClass: 'instructionsScreen-move-2-left',
        //   type: sessionType
        // },
        // 'VerticalMenuDefault' : true,
      })
    }
    
  }

  verticalMenuCall2Parent (arg) {
    // verticalMenuBttnE verticalMenuBttnUp verticalMenuBttnDown verticalMenuBttnLeft verticalMenuBttnRight
    switch (arg) {
      case "closeMenuPopup":
        this.setState({'MenuPopupActive': false});
        // console.log("here we are")
      break;
      case "openMenuPopup":
        this.setState({'MenuPopupActive': true});
      break;
      case "moveBack":
        this.setState({'currentScreen': "introPage",
          'VerticalMenuDefault' : false,
          'InstructionsScreen': {
            visible: false,
            // componentClass: 'instructionsScreen-move-2-left'
          },
          'InitialScreen': {
            visible: true,
            componentClass: 'initialScreen-move-2-right'
          },
        });
      break;
      case "move2Attendance":
        this.setState({'currentScreen': "attendancePage",
          'AttendanceScreen': {
            visible: true,
            componentClass: "attendanceScreen-move-2-up",
            popupActive: true
          },
          'InstructionsScreen': {
            visible: false,
            componentClass: 'instructionsScreen-move-2-left'
          },
          'VerticalMenuContent': "attendanceScreenNoStudents"
        });
      break;

      case "move2Register":
        this.attendanceScreenPopup();
      break;

      // case "stayHereCloseMenu":
      //   this.setState({'MenuPopupActive': false});
      // break;
      
      default:
        console.log("Issue with switch-block of verticalMenuCall2Parent-function in App.js. Calling with an argument: " + arg)
      break;
    }
    // this.setState({'currentScreen': "intro",
    //   'VerticalMenuDefault' : false,
    //   'InstructionsScreen': {
    //     visible: false,
    //     // componentClass: 'instructionsScreen-move-2-left'
    //   },
    //   'InitialScreen': {
    //     visible: true,
    //     componentClass: 'initialScreen-move-2-right'
    //   },
      // 'RegisterScreenClass': "registerScreen-move-2-right",
      // 'InstructionsScreenClass': "instructionsScreen-move-2-right",
      // 'IntroScreenClass': "initialScreen-move-2-right",
      // 'AttendanceScreenClass': "attendanceScreen-move-2-down",
    // })
  }

  attendanceScreenPopup () {
    this.setState({
      'currentScreen': "registerPage",
      'AttendanceScreen': {
        visible: false,
        componentClass: "attendanceScreen-move-2-down",
        popupActive: false
      },
      'RegisterScreen': {
        visible: true,
        componentClass: "registerScreen-move-2-left"},
      VerticalMenuContent: "registerScreen",
    });
  }

  render() {
    // let REACT_VERSION = React.version;
    return (
      <div className="App">

        <div className='debug-version'>ver.{appVersion}</div>
        {/* <div className='debug-version'>React <span style={{color: 'red', opacity: .6}}>{REACT_VERSION}</span></div> */}

        {/* could not be unmounted (or need to keep special states)  */}
        {/* {this.state.InitialScreen.visible ? */}

        <InitialScreen changeClickerState={this.call2Clicker} call2Parent={this.initialScreenCall2Parent} changeClassOfComponent={this.state.InitialScreen.componentClass} />
        
        {/* : null} */}

        {/*{this.state.VirtualClicker ? <VirtualClickerComponent /> : console.log("no Virtual clicker yet")} */}

        {/*
        {this.state.VirtualClicker ? <VirtualClickerComponent host={this.state.hostButtonsStates.bttn4} participant={this.state.participantsButtonsStates.bttn3} minimizeClicker={this.minimizeClicker} maximizeMe={this.state.VirtualClickerMinimized} /> : null}
        */}

        {this.state.VirtualClicker ? <VirtualClickerContainer
            // clickerState={this.state.VirtualClicker}
            minimizeClicker={this.minimizeClicker}
            maximized={this.state.VirtualClickerMaximized}
            // host={this.state.hostButtonsStates.bttn4}
            // participant={this.state.participantsButtonsStates.bttn3}
            host={this.state.VirtualClickerParams.host}
            participant={this.state.VirtualClickerParams.participant}
            /> : null}

        {/* <VirtualClickerComponent call2ShowClicker={this.state.call2VirtualClicker}/> */}

        {this.state.VirtualClicker && !this.state.VirtualClickerMaximized ? <VirtualClickerMinimized slideLeft={this.state.VirtualClickerMinimizedPositionLeft} callToParentScreen={this.maximizeClicker} /> : null}

        {/* this is wrong way to set call2Parent!   ,    ,       ! */}
        {/* {this.state.VerticalMenuDefault ? <VerticalMenuDefault call2Parent={() => this.verticalMenuCall2Parent()} /> : null } */}
        {/* this is right way to set call2Parent! */}
        {this.state.VerticalMenuDefault ? <VerticalMenuDefault call2Parent={this.verticalMenuCall2Parent} signal2Component={this.state.VerticalMenuContent} /> : null }

        {/* TODO: need to finish functionality */}
        
        {/* {this.state.MenuPopupActive ? <MenuPopup call2Parent={this.verticalMenuCall2Parent} call2Component={this.MenuPopupSettings} testProp={this.testFunction}/> : null} */}
        {this.state.MenuPopupActive ? <MenuPopup call2Parent={this.menuPopupCallback} call2Component={this.state.currentScreen} /> : null}

        {/* <InstructionsScreen signal2Parent={this.nextScreen} classManagement={this.state.InstructionsScreenClass} */}

        {/* TODO: need to finish functionality */}
        {this.state.InstructionsScreen.visible ?
        <InstructionsScreen signal2Parent={this.nextScreen} signal2Component={this.state.InstructionsScreen} />
        : null }
        {/* <InstructionsScreen signal2Parent={this.nextScreen} signal2Component={this.state.InstructionsScreenClass} /> */}

        {/* TODO: need to finish functionality */}
        {this.state.RegisterScreen.visible ? <RegisterScreen signal2Parent={this.nextScreen} signal2Component={this.state.RegisterScreen.componentClass} /> : null }

        <AttendanceScreen signal2Parent={this.nextScreen} signal2Component={this.state.AttendanceScreen.componentClass}/>

        {this.state.AttendanceScreen.popupActive ? <AttendanceScreenPopup signal2Parent={this.attendanceScreenPopup} /> : null}

        {this.state.WebSessionScreen.visible ? <WebScreen sessionID={this.state.WebSessionScreen.sessionID} sessionPassword={this.state.WebSessionScreen.sessionPassword} sessionType={this.state.WebSessionScreen.sessionType} signal2Component={this.state.WebSessionScreen.componentClass} signal2Parent={this.WebSessionScreenAction} /> : null }
        

      </div>

    );
  }
}

export default App;
